import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

import liveVideoDetail from '@/view/liveStreaming/live-video-detail.vue'; //视频直播页

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'liveVideoDetail',
    component: liveVideoDetail,
  }, ]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



