// src/view/liveStreaming/live-video-detail.vue  视频直播
<template>
   <div>
      <!-- 视频区 -->
      <div class="topVideo">
          <!-- <iframe class="topVideo" src="http://player.youku.com/embed/XMTQyOTM5MDMwNA==" frameborder=0 allowfullscreen></iframe> -->
          <iframe class="topVideo" src="http://shangzhibo.tv/watch/2828319?player" scrolling="no" allowFullScreen></iframe>
      </div>

      <!-- 推送区 -->
      <div class="container">
         <p class="joinNum"> 19101 人参与</p>
         <div class="switch" @click="handleSwitch($event)">
             <p class="editor" :class="this.view=='liveEditor'?'select':''" >动态</p>
             <p class="chat" :class="this.view=='liveChat'?'select':''" >聊天</p>
         </div>
         <keep-alive> 
             <component :is="view" :liveId='this.liveId' :sort='this.sort' :myOwnId='this.myOwnId'></component>
         </keep-alive>  
      </div>

      <!-- 功能区 -->
      <topAndSort v-on:emitSort="handleSort" />
       

   </div>
</template>

<script>
import appMixins from "@/assets/js/appMixins.js";

import liveEditor from "@/components/livestream/live-video-editor.vue"; // 动态推送
import liveChat from "@/components/livestream/live-video-chat.vue"; // 动态聊天

import topAndSort from "@/components/livestream/topAndSort.vue"; //置顶 , 排序

export default {
  name: "live-video-detail",
  mixins: [appMixins],
  components: { liveEditor, liveChat, topAndSort },
  data() {
    return {
      view: "liveEditor",
      myOwnId: 666,
      liveId: 110,
      //  0 正序 , 1 倒序
      sort: 1
    };
  },
  methods: {
    handleSwitch(e) {
      const dom = e.target;
      if (dom.innerHTML == "动态") {
        this.view = "liveEditor";
      } else {
        this.view = "liveChat";
      }
    },
    handleSort() {
      console.log("父组件触发了");
      this.sort = !this.sort;
    },
    getOwnId(id) {
      this.myOwnId = id;
    }
  },
  mounted() {},
  created() {
    // 排序 goTop 淡入淡出
    window.onscroll = listenScroll();
    function listenScroll() {
      var canRun = true;
      return function() {
        if (!canRun) {
          return;
        }
        canRun = false;
        setTimeout(function() {
          let scrollDistance =
            document.body.scrollTop == 0
              ? document.documentElement.scrollTop
              : document.body.scrollTop;

          let goTopDom = document.getElementsByClassName("sortAndTop")[0];
          if (scrollDistance > 500) {
            if (goTopDom) {
              goTopDom.classList.add("opa");
              var timer = setTimeout(function() {
                goTopDom.classList.add("fadeIn");
              }, 100);
            }
          } else {
            if (goTopDom) {
              goTopDom.classList.remove("fadeIn");
              goTopDom.classList.remove("opa");
              if (timer) {
                clearTimeout(timer);
              }
            }
          }

          canRun = true;
        }, 400);
      };
    }
  }
};
</script>

<style lang='less' scoped>
@m: 750/375;
.topVideo {
  width: 100%;
  height: 210px * @m;
  overflow: hidden;
}
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px * @m solid transparent;
  font-size: 14px * @m;
  margin-top: 10px * @m;
  background: #fefefe;
  height: auto;
}
.joinNum {
  font-size: 13px * @m;
  color: #c6c6c6;
  margin-bottom: 30px * @m;
}
.switch {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  font-size: 18px * @m;
  color: #bac3ce;
  margin-bottom: 30px * @m;
}
.editor {
  margin-right: 29px * @m;
}
.select {
  color: #0f2540 !important;
}
</style>


