<template>
   <div class="sortAndTop">
       <div class="gotoTop" @click="gotoTop">
            top
       </div>
       <div class="sort" @click="reverse">
           sort
       </div>
   </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {};
  },
  methods: {
      gotoTop(){
          document.documentElement.scrollTop = 0;
      },
      reverse(){
         this.$emit('emitSort');
      }
  },
  mounted() {},
  created() {}
};
</script>
<style lang='less' scoped>
@m: 750/375;

.sortAndTop {
  position: fixed;
  z-index: 20;
  bottom: 20px * @m;
  right: 20px * @m;
  display: flex;
  flex-direction: column;

  display: none;
}
.sortAndTop div {
  height: 46px * @m;
  width: 46px * @m;
  background: #ffffff;
  box-shadow: 0 5px 30px 0 rgba(15, 37, 64, 0.2);
  border-radius: 100%;
  text-align: center;
}
.gotoTop {
  margin-bottom: 20px * @m;
}

.opa{
    display: block !important;
    opacity: 0 ;
}
.fadeIn{
    opacity: 1 ;
    transition: all 1s;
}

</style>


